(function(){
    'use strict';

    var MODULE_NAME = 'app.views.shortfuse-config';
    require('angular-ui-router');
    require('services/api/api.services');

    angular.module(MODULE_NAME,[
        'ui.router',
        'app.services'
    ]).config(Config).controller('ShortfuseConfigCtrl',Controller);

    /* @ngInject */
    function Controller($rootScope,$scope,$log,sf_config_data,AppServices) {

        $scope.sf_config = sf_config_data;

        $scope.update_sf_config = function () {
            AppServices.api.shortfuse_configs.update_default($scope.sf_config,function(result){
                $scope.sf_config = result;
            });
        }

        $scope.pruneAudits = function () {

            $rootScope.showActivity = true;
            AppServices.api.audits.prune({},function(result){
                $rootScope.showActivity = false;
            });

        };

        function Init() {

            $log.debug('sf_config: ' + JSON.stringify($scope.sf_config,null,2));
        }

        Init();
    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.shortfuse-config', {
                url: '/shortfuse-config',
                views: {
                    'container@': {
                        template: require('./shortfuse-config.html'),
                        controller: 'ShortfuseConfigCtrl',
                        controllerAs: 'vm'
                    }
                },
                resolve:{
                    sf_config_data:function(AppServices){
                        return AppServices.api.shortfuse_configs.get_default({}).$promise;
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


