(function () {
    'use strict';

    require('angular-ui-router');

    require('services/api/api.services');
    var auditsDirective = require('directives/audits/audits.directive');

    var MODULE_NAME = 'app.views.eta-user';

    angular.module(MODULE_NAME, [
        'ui.router',
        'app.services',
        auditsDirective
    ]).config(Config).controller('EtaUserCtrl', Controller);

    /* @ngInject */
    function Controller($rootScope, $scope, $log, eta_user_data, AppServices) {
        $scope.eta_user = eta_user_data;
        $scope.audits = [];

        //$log.debug('$scope.eta_user : ' + JSON.stringify($scope.eta_user, null, 2));

        function Init() {

            //$log.debug('user_scac_data: ' + JSON.stringify(user_scac_data,null,2));
            $rootScope.showActivity = true;

            AppServices.api.eta_users.get_default_audits({}, function (result) {

                $scope.audits = result;
                $rootScope.showActivity = false;
            })

        };

        $scope.update_eta_user = function () {

        }

        Init();
    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.eta-user', {
                url: '/eta-user',
                views: {
                    'container@': {
                        template: require('./eta-user.html'),
                        controller: 'EtaUserCtrl',
                        controllerAs: 'vm'
                    }
                },
                resolve: {
                    eta_user_data: function (AppServices) {
                        return AppServices.api.eta_users.get_default({}).$promise;
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


