(function(){
    'use strict';

    var MODULE_NAME = 'app.views.shipment-rules';
    require('angular-ui-router');
    require('services/api/api.services');

    angular.module(MODULE_NAME,[
        'ui.router',
        'app.services'
    ]).config(Config).controller('ShipmentRulesCtrl',Controller);

    /* @ngInject */
    function Controller($scope,$log,shipment_rule_data,AppServices) {

        $scope.shipment_rule = shipment_rule_data;
        //$log.debug('$scope.shipment_rule: ' + JSON.stringify($scope.shipment_rule,null,2));

        $scope.update_shipment_rule = function () {
            AppServices.api.shipment_rules.update_default($scope.shipment_rule,function(result){
                $scope.shipment_rule = result;
            });
        }
    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.shipment-rules', {
                url: '/shipment-rules',
                views: {
                    'container@': {
                        template: require('./shipment-rules.html'),
                        controller: 'ShipmentRulesCtrl',
                        controllerAs: 'vm'
                    }
                },
                resolve:{
                    shipment_rule_data:function(AppServices){
                        return AppServices.api.shipment_rules.get_default({}).$promise;
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


