(function(){
    'use strict';

    var MODULE_NAME = 'app.views.shipments';

    require('angular-ui-router');
    require('oclazyload');

    var shipmentsDirective = require('directives/shipments/shipments.directive');

    angular.module(MODULE_NAME,[
        'ui.router',
        'oc.lazyLoad',
        shipmentsDirective
    ]).config(Config).run(Run).controller('ShipmentsCtrl',Controller);

    /* @ngInject */
    function Run($rootScope, $ocLazyLoad) {

        // Let's load the next page that the user might need asynchronously
        $rootScope.$evalAsync(function () {

            require.ensure([], function () {
                require('./shipment-details/shipment-details');
                $ocLazyLoad.load({name: 'app.views.shipment-details'});
            });
        });
    }

    /* @ngInject */
    function Controller($rootScope,$scope,$log,$interval,AppServices) {

        $scope.s_created = []
        $scope.s_error = [];
        $scope.s_declined = [];
        $scope.s_locked = [];
        $scope.s_accepted = [];

        $scope.refreshShipments = function () {
            $rootScope.showActivity = true;
            AppServices.api.shipments.query({app_state:'created'},function(result,headers){

                $scope.s_created = result;
                AppServices.api.shipments.query({app_state:'error'},function(result,headers){

                    $scope.s_error = result;
                    AppServices.api.shipments.query({app_state:'declined'},function(result,headers){

                        $scope.s_declined = result;
                        AppServices.api.shipments.query({app_state:'locked_by_other'},function(result,headers){

                            $scope.s_locked = result;
                            AppServices.api.shipments.query({app_state:'accepted'},function(result,headers){

                                $scope.s_accepted = result;
                                $rootScope.showActivity = false;

                            });

                        });

                    });

                });


            });
        }

        var Init = function () {

            $scope.refreshShipments();

            $interval($scope.refreshShipments,30000);

        }

        Init();
    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.shipments', {
                url: '/shipments',
                views: {
                    'container@': {
                        template: require('./shipments.html'),
                        controller: 'ShipmentsCtrl',
                        controllerAs: 'vm'
                    }
                }
            });
    };

    module.exports = MODULE_NAME;

})();


