(function () {
    'use strict';
    require('angular-ui-router');

    require('datatables.net');
    require('angular-datatables/dist/angular-datatables');
    require('angular-datatables/dist/css/angular-datatables.css');
    require('material-design-lite/material.css');
    require('dataTables.material');
    require('dataTables.material.css');

    require('oclazyload');

    require('services/api/api.services');

    var MODULE_NAME = 'app.views.user-scacs';

    angular.module(MODULE_NAME, [
        'app.services',
        'datatables',
        'oc.lazyLoad',
        'ui.router'
    ]).config(Config).run(Run).controller('UserScacsCtrl', Controller);

    /* @ngInject */
    function Controller($rootScope, $scope, $log, $state, $timeout, $q, DTOptionsBuilder, DTColumnDefBuilder, AppServices) {

        function rowCallback(nRow, aData, iDisplayIndex, iDisplayIndexFull) {
            // Unbind first in order to avoid any duplicate handler (see https://github.com/l-lin/angular-datatables/issues/87)
            $('td', nRow).unbind('click');
            $('td', nRow).bind('click', function () {
                $scope.$apply(function () {
                    $scope.onRowSelect(aData);
                });
            });
            return nRow;
        }

        $scope.onRowSelect = function (item) {
            //$log.debug('item: ' + JSON.stringify(item, null, 2));

            $state.go('app.user-scac-details', {id: item[0]});
        };

        $scope.data = [];

        $scope.AppServices = AppServices;

        $scope.dtOptions = DTOptionsBuilder.newOptions()
            .withPaginationType('simple_numbers')
            .withOption('rowCallback', rowCallback)
            .withOption('searching', false)
            .withOption('order', [[ 3, "asc" ],[ 0, "asc" ]]);
        $scope.dtColumnDefs = [
            DTColumnDefBuilder.newColumnDef(0).notVisible(),
            DTColumnDefBuilder.newColumnDef(1).withOption('className', 'mdl-data-table__cell--non-numeric'),
            DTColumnDefBuilder.newColumnDef(2).withOption('className', 'mdl-data-table__cell--non-numeric'),
            DTColumnDefBuilder.newColumnDef(3).withOption('className', 'mdl-data-table__cell--non-numeric')
        ];

        function Init() {

            $scope.refreshUserScacs();
        };

        $scope.isDisabled = function (user_scac) {

            return AppServices.api.user_scacs.isDisabled(user_scac);
        };


        $scope.startAll = function (){

            $rootScope.showActivity = true;
            var promises = [];
            $scope.data.forEach(function(user_scac){

                if (!AppServices.api.user_scacs.isDisabled(user_scac)) {
                    var promise = $scope.startQuery(user_scac);
                    promises.push(promise);
                }

            });
            $q.all(promises).then(function(){
                $rootScope.showActivity = false;
            });


        };

        $scope.startQuery = function (user_scac) {

            return AppServices.api.user_scacs.start_query({id:user_scac.id}).$promise;

        };

        $scope.updateScacs = function () {

            $rootScope.showActivity = true;
            return AppServices.api.user_scacs.fetch_default_scacs({}).$promise.then(function (result) {

                //$log.debug('AppServices.api.user_scacs.fetch_default_scacs: ' + JSON.stringify(result,null,2));
                $scope.data = result;

            }).catch(function (error) {

                $log.error('AppServices.api.user_scacs.fetch_default_scacs: ' + error)

            }).finally(function () {
                $rootScope.showActivity = false;
            });
        };

        $scope.refreshUserScacs = function () {
            $rootScope.showActivity = true;
            //$log.debug('user_scacs_data: ' + JSON.stringify(user_scacs_data,null,2));
            AppServices.api.user_scacs.query({}, function (result) {
                $scope.data = result;
                $rootScope.showActivity = false;
            });
        };


        Init();

    };

    /* @ngInject */
    function Config($stateProvider) {
        $stateProvider
            .state('app.user-scacs', {
                url: '/user-scacs',
                views: {
                    'container@': {
                        template: require('./user-scacs.html'),
                        controller: 'UserScacsCtrl',
                        controllerAs: 'vm'
                    }
                }
            });
    };

    /* @ngInject */
    function Run($rootScope, $ocLazyLoad) {

        // Let's load the next page that the user might need asynchronously
        $rootScope.$evalAsync(function () {

            require.ensure([], function () {
                require('./user-scac-details/user-scac-details');
                $ocLazyLoad.load({name: 'app.views.user-scac-details'});
            });
        });
    }

    module.exports = MODULE_NAME;

})();


